import { SigninPersonalInformationComponent } from './signin-personal-information/signin-personal-information';
import { SigninCompanyInformationComponent } from './signin-company-information/signin-company-information';
import { LoginSigninComponent } from './login-signin/login-signin';
import { ProfileDashboardComponent } from './profile-dashboard/profile-dashboard';
import { ProfileEditDashboardComponent } from './profile-edit-dashboard/profile-edit-dashboard';
import { ProfileHeaderDashboardComponent } from './profile-header-dashboard/profile-header-dashboard';
@NgModule({
	declarations: [LoginComponent,
    HeaderAuthenticationComponent,
    SigninUserInformationComponent,
    SigninPersonalInformationComponent,
    SigninCompanyInformationComponent,
    LoginSigninComponent,
    LoginSigninComponent,
    ProfileDashboardComponent,
    ProfileEditDashboardComponent,
    ProfileHeaderDashboardComponent],
	imports: [
		IonicModule
	],
	exports: [LoginComponent,
    HeaderAuthenticationComponent,
    SigninUserInformationComponent,
    SigninPersonalInformationComponent,
    SigninCompanyInformationComponent,
    LoginSigninComponent,
    LoginSigninComponent,
    ProfileDashboardComponent,
    ProfileEditDashboardComponent,
    ProfileHeaderDashboardComponent]
})
export class ComponentsModule {}



