import {Component} from '@angular/core';
import { SigninProvider} from "../../providers/signin/signin";

/**
 * Generated class for the SigninUserInformationComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
    selector: 'signin-user-information',
    templateUrl: 'signin-user-information.html'
})
export class SigninUserInformationComponent {

    mail: string;
    password: string;
    passwordConfirm: string;

    constructor(public SigninProvider: SigninProvider) {
        console.log('Hello SigninUserInformationComponent Component');

    }

    singinUserInformation() {
        var userInformation = {
            mail: this.mail,
            password: this.password,
            passwordConfirm: this.passwordConfirm
        };

        this.SigninProvider.signin_userInformation(userInformation).then(
            (resolve: { login }) => {
                if (resolve.login != null) {
                    // <[AGEXPORT PLUS][PRUEBAS]
                    if (this.mail == "prueba" && this.password == "prueba" && this.passwordConfirm == "prueba") {

                    }
                    // [AGEXPORT PLUS][PRUEBAS]>
                }
            }
        )
    }

}



