import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable()
export class SigninProvider {
  apiUrl = "http://aeapi.iflexsoftware.com";

  constructor(public http: HttpClient) {
    console.log('Hello SigninProvider Provider');
  }

  public signin_userInformation(userInformation){
    return new Promise((resolve,reject) => {
      this.http.post(this.apiUrl + '/contact/helpLogin.json/', JSON.stringify(userInformation), {
        headers: new HttpHeaders().set('Content-type', 'application/json; charset=utf-8')
      }).subscribe(
          data => {
            resolve(data)
          },
          error => reject(error.error.error.message),
      )
    })
  }

  public signin_personalInformation(personalInformation){
    return new Promise((resolve,reject) => {
      this.http.post(this.apiUrl + '/contact/helpLogin.json/', JSON.stringify(personalInformation), {
        headers: new HttpHeaders().set('Content-type', 'application/json; charset=utf-8')
      }).subscribe(
          data => {
            resolve(data)
          },
          error => reject(error.error.error.message),
      )
    })
  }

  public signin_companyInformation(companyInformation){
    return new Promise((resolve,reject) => {
      this.http.post(this.apiUrl + '/contact/helpLogin.json/', JSON.stringify(companyInformation), {
        headers: new HttpHeaders().set('Content-type', 'application/json; charset=utf-8')
      }).subscribe(
          data => {
            resolve(data)
          },
          error => reject(error.error.error.message),
      )
    })
  }

}



